import jsonp from 'common/js/jsonp'

const commonParams = {
  g_tk: 916037377,
  inCharset: 'utf-8',
  outCharset: 'utf-8',
  notice: 0
}

const options = {
  param: 'jsonpCallback',
  prefix: 'jp'
}

export function getSingerList() { //jsonp请求
  const url = 'https://c.y.qq.com/v8/fcg-bin/v8.fcg'
  
  const data = Object.assign({}, commonParams, {
    channel: 'singer',
    page: 'list',
    key: 'all_all_all',
    pagesize: 100,
    pagenum: 1,
    hostUin: 0,
    needNewCode: 0,
    platform: 'yqq',
    format: 'jsonp'
  })

  return jsonp(url, data, options)
}

export function getSingerDetail(singerId) { //注意要传参数 同上 jsonp请求
  const url = 'https://c.y.qq.com/v8/fcg-bin/fcg_v8_singer_track_cp.fcg'

  const data = Object.assign({}, commonParams, {
    hostUin: 0,
    needNewCode: 0,
    platform: 'yqq',
    format: 'jsonp',
    order: 'listen',
    begin: 0,
    num: 100,
    songstatus: 1,
    singermid: singerId
  })

  return jsonp(url, data, options)
}


